import React from "react";
import { ThemeProvider } from "styled-components";
import { GlobalStyle } from "@rk/design-system";
import { ThemeSwitch } from "./../components";
import theme from "./../../config/theme";
// import { useLocalStorage } from "./../hocs";

const Root = ({ children }) => {
  const [themeMode, setThemeMode] = React.useState("light"); //useLocalStorage("themeMode", "light");
  const globaltheme = {
    ...theme,
    colors: {
      ...theme[themeMode].colors
    }
  };
  return (
    <ThemeProvider theme={globaltheme}>
      <>
        <GlobalStyle />
        <ThemeSwitch
          themeMode={themeMode}
          toggleTheme={() => {
            if (themeMode === "light") {
              setThemeMode("dark");
            } else {
              setThemeMode("light");
            }
          }}
        />
        {children}
      </>
    </ThemeProvider>
  );
};

export default Root;
