import React from "react";
import { Icon } from "./../../components";

export const ThemeSwitch = ({ themeMode, toggleTheme }) => {
  return (
    <div
      className="d-print-none"
      style={{
        position: "fixed",
        top: ".5rem",
        right: "1rem",
        width: "2.5rem",
        height: "2.5rem",
        zIindex: 99999
      }}
    >
      <button
        className="btn"
        style={{
          position: "absolute",
          marginLeft: "-.25rem"
        }}
        onClick={toggleTheme}
      >
        <Icon
          style={{ color: themeMode === "light" ? "orange" : "#fff" }}
          name={themeMode}
          size="lg"
        />
      </button>
    </div>
  );
};
